'use client';

import { useEffect, useState } from 'react';
import { Order } from '@/lib/types';
import LoginModal from '@/components/login-user';
import { Button } from '@/components/ui/button';

export default function CreditCustomersPage() {
    const [orders, setOrders] = useState<Order[]>([]);
    const [loading, setLoading] = useState(true);
    const [showLogin, setShowLogin] = useState(false);

    useEffect(() => {
        const loggedIn = localStorage.getItem('isLoggedIn');
        if (loggedIn !== 'true') {
            setShowLogin(true);
        } else {
            fetchOrders();
        }
    }, []);

    const fetchOrders = async () => {
        try {
            const response = await fetch('/api/orders');
            const data = await response.json();
            setOrders(data);
        } catch (error) {
            console.log('Error loading orders', error);
        } finally {
            setLoading(false);
        }
    };

    const handlePayment = async (orderId: string, amount: number) => {
        const payDate = new Date();  

        await fetch(`/api/orders/${orderId}/pay`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ amount, date: payDate }),
        });

        fetchOrders();
    };

    if (loading) return <p className="p-10">Loading...</p>;

    const activeCustomers = orders.filter(o => o.paymentStatus !== 'fully-paid');
    const fullyPaidCustomers = orders.filter(o => o.paymentStatus === 'fully-paid');

    return (
        <div className="p-10">
            {showLogin && <LoginModal onLoginSuccess={() => setShowLogin(false)} />}

            <h1 className="text-3xl font-bold mb-6">Kredit Mijozlar</h1>

            {/* ---------------- ACTIVE CUSTOMERS ---------------- */}
            <h2 className="text-2xl font-semibold mt-10 mb-4">Aktiv Kredit Mijozlar</h2>

            <div className="overflow-x-auto">
                <table className="w-full border-collapse border">
                    <thead>
                        <tr className="bg-gray-200">
                            <th className="border p-2">Mijoz</th>
                            <th className="border p-2">Mahsulotlar</th>
                            <th className="border p-2">Umumiy summa</th>
                            <th className="border p-2">To'langan</th>
                            <th className="border p-2">Qolgan</th>
                            <th className="border p-2">Status</th>
                            <th className="border p-2">Keyingi to'lov</th>
                            <th className="border p-2">To'lash</th>
                        </tr>
                    </thead>

                    <tbody>
                        {activeCustomers.map(order => {
                            const remaining = order.agreedAmount - order.paidAmount;

                            return (
                                <tr key={order.id}>
                                    <td className="border p-2">{order.clientName}</td>

                                    <td className="border p-2">
                                        {order.products.map(p => (
                                            <div key={p.productId}>
                                                {p.productName} x {p.quantity}
                                            </div>
                                        ))}
                                    </td>

                                    <td className="border p-2">{order.agreedAmount} so'm</td>
                                    <td className="border p-2">{order.paidAmount} so'm</td>
                                    <td className="border p-2">{remaining} so'm</td>
                                    <td className="border p-2">{order.paymentStatus}</td>

                                    <td className="border p-2">
                                        {order.payments.length > 0
                                            ? new Date(order.payments[order.payments.length - 1].date)
                                                .toLocaleDateString()
                                            : 'Biror toʻlov yoʻq'}
                                    </td>

                                    <td className="border p-2 text-center">
                                        <Button
                                            className="bg-green-600 text-white px-3 py-1 rounded"
                                            onClick={() => handlePayment(order.id, order.monthlyPayment)}
                                        >
                                            To'lash
                                        </Button>
                                    </td>
                                </tr>
                            );
                        })}
                    </tbody>
                </table>
            </div>

            {/* ---------------- FULLY PAID CUSTOMERS ---------------- */}
            <h2 className="text-2xl font-semibold mt-16 mb-4 text-green-700">To'liq To'lagan Mijozlar</h2>

            <div className="overflow-x-auto">
                <table className="w-full border-collapse border">
                    <thead>
                        <tr className="bg-green-200">
                            <th className="border p-2">Mijoz</th>
                            <th className="border p-2">Mahsulotlar</th>
                            <th className="border p-2">Umumiy summa</th>
                            <th className="border p-2">Holati</th>
                            <th className="border p-2">To‘lovlar soni</th>
                            <th className="border p-2">Oxirgi to‘lov sanasi</th>
                        </tr>
                    </thead>

                    <tbody>
                        {fullyPaidCustomers.map(order => (
                            <tr key={order.id} className="bg-green-50">
                                <td className="border p-2">{order.clientName}</td>

                                <td className="border p-2">
                                    {order.products.map(p => (
                                        <div key={p.productId}>
                                            {p.productName} x {p.quantity}
                                        </div>
                                    ))}
                                </td>

                                <td className="border p-2">{order.agreedAmount} so'm</td>

                                <td className="border p-2 text-green-700 font-bold">To‘liq to‘langan</td>

                                <td className="border p-2">{order.payments.length} ta</td>

                                <td className="border p-2">
                                    {order.payments.length > 0
                                        ? new Date(order.payments[order.payments.length - 1].date)
                                            .toLocaleDateString()
                                        : '—'}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

        </div>
    );
}